html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

.lazyload, .lazyloading {
  opacity: 0;
  transition: all 1s;
  transform: translateX(-100%);
}

.lazyloaded {
  opacity: 1;
  transition: transform 1s, opacity 1s;
  transform: translateY(0);
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

body {
  background-color: rgba(38, 36, 36, .918);
  background-image: url("background-fon-2.6a73f418.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  margin: 0;
  font-family: Aboreto, Arial, Helvetica, sans-serif;
  font-size: 14px;
}

body.stop-fon {
  overflow: hidden;
}

body.white-theme {
  background-image: url("background-white-theme.e83040f9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.page-header {
  width: 100%;
  z-index: 10;
  background-color: rgba(11, 12, 12, .7);
  margin-bottom: 5px;
  padding-left: 20px;
  padding-right: 20px;
  position: fixed;
  top: 0;
  right: 0;
}

.page-header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  color: gold;
  text-shadow: 0 0 10px #06e6f6;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: Aboreto;
  font-size: 27px;
  font-weight: bold;
}

.logo:focus {
  outline: none;
  transform: scale(1.1);
}

body.white-theme .logo {
  text-shadow: 0 0 10px #f20a0a;
}

body.white-theme .librarry-btn, body.white-theme .home-btn {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.librarry-btn, .home-btn {
  cursor: pointer;
  text-transform: uppercase;
  max-width: 100px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-family: Aboreto;
  font-size: 12px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.librarry-btn:hover, .librarry-btn:focus, .home-btn:hover, .home-btn:focus {
  outline: none;
  transform: scale(1.1);
}

.search-form {
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  display: flex;
}

.input-search {
  width: 200px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  padding-left: 10px;
  padding-right: 10px;
  font-family: Aboreto;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7), inset 0 0 30px 10px rgba(6, 230, 246, .7);
}

.input-search:focus {
  transform: scale(1.1);
}

body.white-theme .input-search {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.input-search::-ms-input-placeholder {
  color: gold;
}

.input-search::placeholder {
  color: gold;
}

.search-button {
  cursor: pointer;
  width: 90px;
  height: 30px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  padding: 5px;
  font-family: Aboreto;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7), inset 0 0 30px 10px rgba(6, 230, 246, .7);
}

.search-button:focus {
  transform: scale(1.1);
}

body.white-theme .search-button {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.genre-list {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-bottom: 40px;
  padding: 10px;
  font-family: Aboreto;
  list-style: none;
  display: flex;
}

.genre-item {
  cursor: pointer;
  width: 190px;
  color: gold;
  text-transform: uppercase;
  background-color: rgba(10, 10, 10, .624);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-size: 21px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.genre-item:hover {
  transform: scale(1.04);
}

body.white-theme .genre-item {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.movie-card__item {
  max-width: 200px;
  background-color: rgba(255, 227, 194, .3);
  border-radius: 10px;
  transition: transform .3s, box-shadow .3s, filter .3s;
}

.movie-card__item:hover {
  filter: saturate(165%);
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

body.white-theme .movie-card__item {
  background-color: rgba(14, 14, 13, .7);
}

body.white-theme .movie-card__item:hover {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.poster-wrapper {
  overflow: hidden;
}

.movie-card__poster {
  cursor: pointer;
  border-radius: 10px 10px 0 0;
}

.movie-card__poster:hover {
  transform: scale(1.1);
}

.card-info {
  pointer-events: none;
  flex-direction: column;
  padding: 10px;
  display: flex;
}

.movie-info-title {
  text-overflow: ellipsis;
  width: 160px;
  height: 1.2em;
  white-space: nowrap;
  color: gold;
  text-align: center;
  margin-bottom: 5px;
  font-size: 14px;
  overflow: hidden;
}

.info-wrapper {
  justify-content: space-between;
  align-items: baseline;
  margin-top: auto;
  display: flex;
}

.movie-icon-decor {
  margin-bottom: 5px;
}

.info-item-year, .info-item-language {
  color: gold;
  margin-bottom: 5px;
  font-size: 12px;
}

.rating-icon {
  color: #daa520;
}

.info-item-rating {
  color: #ccc8c8;
}

.modal {
  max-width: 700px;
  background-color: rgba(255, 228, 196, .8);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

body.white-theme .modal {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.img-container {
  max-width: 50%;
}

.modal-img {
  width: 100%;
  border-radius: 10px 0 0 10px;
}

.modal-wrapper {
  display: flex;
}

.modal-info {
  width: 50%;
  flex-direction: column;
  row-gap: 20px;
  padding: 10px;
  display: flex;
}

.modal-title {
  font-size: 18px;
}

.moda-btn-wrapper {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

.modal-btn {
  cursor: pointer;
  max-width: 130px;
  color: gold;
  text-shadow: 0 0 5px #1f3514;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-family: Aboreto;
  transition: transform .3s;
  box-shadow: 0 0 5px 5px rgba(6, 230, 246, .7);
}

.modal-btn:focus, .modal-btn:hover {
  outline: none;
  transform: scale(1.1);
}

body.white-theme .modal-btn {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.mySwiper {
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

body.white-theme .mySwiper {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.ganre-wrapper {
  column-gap: 10px;
  padding: 10px;
}

.rater-slide {
  cursor: pointer;
  max-width: 180px;
  border-radius: 10px;
  padding: 10px;
  transition: transform .3s, box-shadow .3s, filter .3s;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.rater-slide:hover {
  filter: saturate(165%);
  transform: scale(1.02);
}

body.white-theme .swiper-slide {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.rated-title-wrapper {
  max-width: 100%;
  min-height: 100px;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  display: flex;
  position: relative;
}

.rated-title {
  color: gold;
  text-shadow: 0 0 20px #c9a22d;
  font-family: Aboreto;
  font-size: 42px;
  font-weight: 700;
  position: absolute;
}

body.white-theme .rated-title {
  text-shadow: 0 0 10px #f20a0a;
}

.rated-img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 5px;
}

.rated-info-box {
  padding: 5px;
}

body.white-theme .rated-info-box {
  background-color: rgba(14, 14, 13, .6);
}

.rated-name {
  color: gold;
  text-overflow: ellipsis;
  width: 150px;
  height: 1.2em;
  white-space: nowrap;
  text-align: center;
  font-family: Aboreto;
  overflow: hidden;
}

@font-face {
  font-family: Anton;
  src: url("Anton-Regular.c9331b93.woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Oswald;
  src: url("Oswald-Regular.d9954873.woff2");
  font-weight: normal;
  font-style: normal;
}

.button-sroll-up {
  position: relative;
}

.btn-scroll {
  width: 30px;
  height: 30px;
  opacity: .7;
  z-index: 200;
  transition: all .3;
  background-color: red;
  border-radius: 30px;
  font-size: 0;
  display: none;
  position: fixed;
  bottom: 10px;
  right: 10px;
  box-shadow: 0 0 10px 10px rgba(201, 162, 45, .7), inset 0 0 30px 10px rgba(201, 162, 45, .7);
}

body.white-theme .btn-scroll {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.btn-scroll.shown {
  display: block;
}

.btn-scroll:before {
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("pngwing.com.3054d48a.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-scroll:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7), inset 0 0 30px 10px rgba(6, 230, 246, .7);
}

.btn-scroll:active {
  transform: scale(1.2);
}

.theme-btn {
  width: 30px;
  background: #2a2c2d;
  background-color: inherit;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.theme-btn button {
  color: #000;
  background: #0060df;
  border: 0;
  border-radius: 5px;
  padding: 10px 20px;
}

.switch {
  width: 45px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch:focus {
  outline: none;
  transform: scale(1.1);
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: rgba(204, 204, 204, .3);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 25px;
  width: 25px;
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center / 16px no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: rgba(204, 204, 204, .3);
}

input:checked + .slider:before {
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center / 20px no-repeat;
  transform: translateX(20px);
}

.slider.round {
  border-radius: 15px;
}

.slider.round:before {
  border-radius: 50%;
}

.skiptranslate {
  height: 0;
  display: none;
}

.language__img {
  cursor: pointer;
}

.cont-lang {
  position: relative;
}

.wrapper-language {
  z-index: 50;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  width: 150px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-left: 50px;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
  transition: transform .3s;
  position: relative;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.wrapper-language:hover, .wrapper-language:focus {
  outline: none;
  transform: scale(1.1);
}

body.white-theme .wrapper-language {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.language {
  width: 100%;
  pointer-events: none;
  color: gold;
  text-shadow: 0 0 1px #06e6f6;
  background-color: rgba(13, 13, 13, .3);
  border-radius: 10px;
  padding: 5px;
  transition: all .4s;
  position: absolute;
  top: 100%;
  left: 0;
  transform: perspective(600px)translate(0, -50%)rotateX(45deg)scale(0);
}

.language.shows {
  pointer-events: painted;
  transform: perspective(600px)translate(0)rotateX(0deg)scale(1);
}

.item-language {
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(9, 137, 235, .7);
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
  transition: transform .3s;
  display: flex;
}

.item-language:hover {
  transform: scale(1.02);
}

.item-language:not(:last-child) {
  margin-bottom: 5px;
}

.back-drop-modal {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 25;
  background-color: rgba(0, 0, 0, .4);
  background-image: url("background-fon.48f0c9b5.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: hidden;
}

.back-drop-modal.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal-window {
  max-width: 528px;
  z-index: 30;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-sing-form {
  flex-direction: column;
  row-gap: 10px;
  margin-bottom: 50px;
  display: flex;
}

.input-email, .input-password, .btn-register, .btn-sign {
  max-width: 100%;
  height: 30px;
  color: #1a1919;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  padding: 3px;
  font-family: Aboreto;
  transition: transform .4s;
  box-shadow: 0 0 10px 10px rgba(201, 162, 45, .9), inset 0 0 30px 10px rgba(201, 162, 45, .9);
}

.input-email:hover, .input-email:focus, .input-password:hover, .input-password:focus, .btn-register:hover, .btn-register:focus, .btn-sign:hover, .btn-sign:focus {
  transform: scale(1.1);
}

.title-modal-sign {
  text-align: center;
  color: #ffb700;
  text-shadow: 0 0 5px #1f3514;
  margin-bottom: 25px;
  font-size: 36px;
}

.btn-wrapper {
  gap: 20px;
  display: flex;
}

.btn-modal-sign, .btn-modal-sign-git {
  cursor: pointer;
  width: 210px;
  height: 30px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-family: Aboreto;
  transition: transform .4s;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px 10px rgba(201, 162, 45, .9), inset 0 0 30px 10px rgba(201, 162, 45, .9);
}

.btn-modal-sign:hover, .btn-modal-sign:focus, .btn-modal-sign-git:hover, .btn-modal-sign-git:focus {
  transform: scale(1.1);
}

.btn-modal-sign:before, .btn-modal-sign-git:before {
  content: " G";
  color: red;
  font-size: 20px;
  display: block;
  position: absolute;
  right: 10px;
}

.btn-modal-sign-git:before {
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("icons8-github.cb99f071.svg");
  background-position: center;
  background-size: cover;
  display: block;
}

.btn-log-out {
  cursor: pointer;
  max-width: 80px;
  height: 30px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-family: Aboreto;
  font-size: 12px;
  transition: transform .4s;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.btn-log-out:hover, .btn-log-out:focus {
  transform: scale(1.1);
}

body.white-theme .btn-log-out {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.preloader {
  width: 100%;
  height: 100%;
  z-index: 9999;
  opacity: 1;
  visibility: visible;
  background: rgba(18, 18, 18, .64);
  justify-content: center;
  align-items: center;
  transition: opacity 1s, visibility;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.preloader_hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s, visibility 0s 1s;
}

.sk-chase {
  width: 128px;
  height: 128px;
  animation: sk-chase 2.5s linear infinite both;
  position: relative;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  animation: sk-chase-dot 2s ease-in-out infinite both;
  position: absolute;
  top: 0;
  left: 0;
}

.sk-chase-dot:before {
  content: "";
  width: 25%;
  height: 25%;
  background-color: #3626a7;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s ease-in-out infinite both;
  display: block;
}

.sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3) {
  animation-delay: -.9s;
}

.sk-chase-dot:nth-child(4) {
  animation-delay: -.8s;
}

.sk-chase-dot:nth-child(5) {
  animation-delay: -.7s;
}

.sk-chase-dot:nth-child(6) {
  animation-delay: -.6s;
}

.sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3):before {
  animation-delay: -.9s;
}

.sk-chase-dot:nth-child(4):before {
  animation-delay: -.8s;
}

.sk-chase-dot:nth-child(5):before {
  animation-delay: -.7s;
}

.sk-chase-dot:nth-child(6):before {
  animation-delay: -.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes sk-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}

@keyframes sk-chase-dot-before {
  50% {
    transform: scale(.4);
  }

  100%, 0% {
    transform: scale(1);
  }
}

